<style scoped>
* {
  margin: 0;
  padding: 0;
}

.box {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  overflow-y: scroll;
  max-height: 55vh;
  z-index: 100;
}



.box1 {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  max-height: 75vh;
  overflow-y: scroll;
  z-index: 999;
}

.content {
  height: 50px;
  width: 100%;
  border: 1px solid rgb(233, 228, 228);
  border-radius: 5px;
  margin: auto;
  margin-top: 10px;
  padding: 10px;
}

.content:hover {
  box-shadow: 0px 2px 10px rgba(0, 0, 30, 0.5);
}

.h4 {
  width: 30px;
  height: 30px;
  line-height: 20px;
  text-align: center;
  margin: 20px;
  margin-left: auto;
}

.left {
  float: left;
}

.right {
  float: right;
  margin-right: 10px;
}

.button {
  position: absolute;
  bottom: 15px;
  width: 80%;
  border-radius: 6px;
  margin: auto;
}

#int {
  width: 102%;
  height: 50px;
  box-shadow: 0px 2px 10px rgba(180, 180, 184, 0.5);
  padding-left: 28px;
  border: none;
  outline: none;
  border-radius: 6px;
}

.button svg {
  position: absolute;
  top: 12px;
  left: 3px;
  bottom: 36px;
  z-index: 999;
}
</style>
<template>
  <div :class="tmp ? 'box' : 'box1'">
    <div class="content" v-for="(item, index) in arrList" :key="index">
      <div class="left">
        <input type="checkbox" v-model="item.isChecked" name="" />
        <span :style="{
          'text-decoration': item.isChecked ? 'line-through' : 'none',
          color: item.isChecked ? 'gray' : 'black',
        }">{{ item.name }}</span><br />
        <div class="block">
          <el-date-picker v-model="item.value" type="date" placeholder="-">
          </el-date-picker>
        </div>
      </div>
      <div class="right">
        <span style="margin: 10px" @click="mover(index)">
          <svg t="1692513885724" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"
            p-id="10274" width="20" height="20">
            <path
              d="M380 455a8 8 0 0 1 8-8h64a8 8 0 0 1 8 8v240a8 8 0 0 1-8 8h-64a8 8 0 0 1-8-8V455zM644 455a8 8 0 0 0-8-8h-64a8 8 0 0 0-8 8v240a8 8 0 0 0 8 8h64a8 8 0 0 0 8-8V455z"
              fill="#8a8a8a" p-id="10275"></path>
            <path
              d="M321 212V96c0-17.673 14.327-32 32-32h320c17.673 0 32 14.327 32 32v116h183a8 8 0 0 1 8 8v64a8 8 0 0 1-8 8h-55v635c0 17.673-14.327 32-32 32H225c-17.673 0-32-14.327-32-32V292h-58a8 8 0 0 1-8-8v-64a8 8 0 0 1 8-8h186z m80-68v68h224v-68H401zM273 292v587h480V292H273z"
              fill="#8a8a8a" p-id="10276"></path>
          </svg>
        </span>
      </div>
    </div>

    <div class="button">
      <input type="text" id="int" placeholder="添加任务" v-model="text" @keyup.enter="addText" />
      <svg t="1692513991294" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"
        p-id="11977" width="25" height="25">
        <path
          d="M874.666667 490.666667H533.333333V149.333333a21.333333 21.333333 0 0 0-42.666666 0v341.333334H149.333333a21.333333 21.333333 0 0 0 0 42.666666h341.333334v341.333334a21.333333 21.333333 0 0 0 42.666666 0V533.333333h341.333334a21.333333 21.333333 0 0 0 0-42.666666z"
          p-id="11978" fill="#8a8a8a"></path>
      </svg>
    </div>
  </div>
</template>
<script>
export default {
  name: "ListS",
  data() {
    return {
      text: "",
      value1: "",
      value2: "",
      arrList: [],
      tmp: true,
      isChecked: false,
    };
  },
  mounted() {
    this.$bus.$on("Data", this.dataBtn);
  },
  methods: {
    mover(index) {
      if (confirm("确定删除吗？")) {
        this.arrList.splice(index, 1);
      }
    },
    dataBtn(value) {
      this.tmp = value;
    },
    toggleCheckbox() {
      this.isChecked = !this.isChecked;
    },
    //方法
    addText() {
      if (this.text != '') {
        let addList = {
          id: this.arrList.length,
          name: this.text,
          status: false,
          value: "",
        };
        this.arrList.push(addList);
        this.text = "";
      } else {
        alert('不能是空白')
      }

    },
  },

  watch: {
    //监听
    op() { },
  },
  props: {
    msg: String,
  },
};
</script>